<template>
  <div class="container">

    <div class="jumbotron text-center">
      <h1>Vultron</h1>
      <p class="lead">The ultimate smart contract model based testing framework</p>
      <hr>
      <b-form-file v-model="files" :state="Boolean(file)" accetpt=".sol" placeholder="Choose a file or drop it here..."
        drop-placeholder="Drop file here..." @change="onFileChange" multiple>
      </b-form-file>
      <!-- <b-table striped hover :items="selected"></b-table> -->
      <div>
        <b-button pill variant="primary" @click="OnUpload"> Upload</b-button>
        <b-button pill variant="primary" @click="OnCompile"> Compile</b-button>
      </div>
      <span>{{ log }}</span>

    </div>
  </div>
</template>


<script>
  export default {
    name: "ModelTest",
    data: function () {
      return {
        selected: [],
        log: ""
      };
    },
    methods: {
      onFileChange(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length)
          return;
        console.log(files);
        this.selected = [];
        for (var file of files) {
          this.selected.push({
            contract: file.name
          });
        }
      },
      OnUpload(e) {
        console.log(e);
        this.log = "upload contracts to server:" + JSON.stringify(this.selected);
        // alert(e.toString() + "upload to server:" + JSON.stringify(this.selected));
      },
      OnCompile(e) {
        console.log(e);
        this.log = "compiled:" + JSON.stringify(this.selected);
        // alert(e.toString() + "compiled:" + JSON.stringify(this.selected));
      }
    },
    props: {
      msg: String
    }
  };
</script>

