<template>
  <div class="container">
    <b-form>
      <h1>Vultron</h1>
      <p class="lead">The ultimate smart contract model based testing framework</p>
      <hr>
      <b-form-group>
        <b-form-file v-model="files" :state="Boolean(file)" accetpt=".sol"
          placeholder="Choose a file or drop it here..." drop-placeholder="Drop file here..." @change="onFileChange"
          multiple>
        </b-form-file>
      </b-form-group>
      <!-- <b-table striped hover :items="selected"></b-table> -->
      <b-button block :variant="variant_upload" @click="OnUpload"> Upload</b-button>
        <!-- <b-table striped hover :items="uploaded"></b-table> -->
        <b-button block :variant="variant_compile" @click="OnCompile"> Compile</b-button>
        <!-- <b-table striped hover :items="compiled"></b-table> -->
          <b-form inline   v-if="compiled.length>0">
                <label class="sr-only" for="inline-form-input-name">Name</label>
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0"
      placeholder="Jane Doe"
    ></b-input>

    <label class="sr-only" for="inline-form-input-username">Username</label>
    <b-input-group prepend="@" class="mb-2 mr-sm-2 mb-sm-0">
      <b-input id="inline-form-input-username" placeholder="Username"></b-input>
    </b-input-group>

    <b-form-checkbox class="mb-2 mr-sm-2 mb-sm-0">Remember me</b-form-checkbox>

    <b-button variant="primary">Save</b-button>
            <b-form-select v-model="selected" :options="options"     class="mb-2 mr-sm-2 mb-sm-0" ></b-form-select>
             <b-form-select v-model="selected" :options="options"      class="mb-2 mr-sm-2 mb-sm-0"></b-form-select>
              <b-form-select v-model="selected" :options="options"     class="mb-2 mr-sm-2 mb-sm-0"></b-form-select>
          </b-form>
          <b-button block variant="primary" @click="OnDeploy"> Deploy</b-button>
       
        <b-table striped hover :items="deployed"></b-table>
      <span>{{ log }}</span>
    </b-form>
  </div>
</template>


<script>
  export default {
    name: "Home",
    data: function () {
      return {
        selected: [],
        uploaded: [],
        compiled: [],
        deployed: [],
        selected2: null,
        options: [],
        log: ""
      };
    },
    methods: {
      onFileChange(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length)
          return;
        console.log(files);
        this.selected = [];
        for (var file of files) {
          this.selected.push({
            contract: file.name
          });
        }
        this.uploaded = [];
        this.compiled = [];
        this.deployed = [];
      },
      OnUpload(e) {
        console.log(e);
        this.uploaded = this.selected;
        this.log = "upload contracts to server:" + JSON.stringify(this.selected);
        // alert(e.toString() + "upload to server:" + JSON.stringify(this.selected));
      },
      OnCompile(e) {
        console.log(e);
        this.compiled = this.uploaded;
        this.log = "compiled:" + JSON.stringify(this.selected);
        for (var instance of this.compiled) {
          this.options.push({
            value: instance.contract,
            text: instance.contract
          });
        }
        // alert(e.toString() + "compiled:" + JSON.stringify(this.selected));
      },
      OnDeploy(e) {
        console.log(e);
        this.deployed = this.compiled;

        this.log = "deployed:" + JSON.stringify(this.selected);
        // alert(e.toString() + "compiled:" + JSON.stringify(this.selected));
      }
    },
    computed: {
      variant_upload: function () {
        return this.uploaded.length == 0 ? "primary" : "success";
      },
      variant_compile: function () {
        return this.compiled.length == 0 ? "primary" : "success";
      }
    },
    props: {
      msg: String
    }
  };
</script>

<style scoped lang="scss">
  .container /deep/ {
    @import "~bootstrap-vue/dist/bootstrap-vue";
    @import "~bootstrap/dist/css/bootstrap";
  }
</style>